
	import { defineComponent, ref } from 'vue'
	import { Todo } from '@/types/todo'
	
	export default defineComponent({
		name: 'Head',
		props: {
			addToPlan: {
				type: Function,
				required: true
			}
		},
		setup(props, context) {
			const todoItem = ref('')
			const add = () => {
				const text = todoItem.value
				if(!text.trim()) return
				const todo = {
					id: Date.now(),
					title: text.trim(),
					isCompleted: false
				}
				props.addToPlan(todo)
				todoItem.value = ''
			}
			
			return {
				todoItem,
				add
			}
		}
	})
	
