
	import {
		defineComponent,
		computed
	} from 'vue'
	import {
		Todo
	} from '../types/todo'

	export default defineComponent({
		name: "Footer",
		props: {
			todos: {
				type: Array as() => Todo[],
				required: true
			},
			checkAll: {
				type: Function,
				required: true
			},
			clearCompleted: {
				type: Function,
				required: true
			}
		},
		setup(props, context) {
			const count = computed(() => {
				return props.todos.reduce((pre: number, todo: Todo) => pre + (todo.isCompleted ? 1 : 0), 0)
			})
			const isCheckAll = computed({
				get() {
					return count.value > 0 && count.value === props.todos.length
				},
				set(val) {
					props.checkAll(val)
				}
			})
			return {
				count,
				isCheckAll
			}
		}
	})
